/** @jsx jsx */
import { jsx, css } from "@emotion/react"
import { useItemsState, useItemsDispatch } from "../../items/_state/hooks"
import { findDefaultSelectVal, updateQueryParamsWithSort } from "Utils"
import Selects from "../selects"

function StorefrontSorting({ isFilteringCollections }) {
  const itemsDispatch = useItemsDispatch()
  const itemsState = useItemsState()

  const options = isFilteringCollections
    ? itemsState.settings.sortingOptionsCollections
    : itemsState.settings.sortingOptionsProducts

  function customOnChange(data) {
    const params = updateQueryParamsWithSort(data.value)

    itemsDispatch({
      type: "SET_QUERY_PARAMS",
      payload: {
        ...itemsState.queryParams,
        ...params,
      },
    })

    itemsDispatch({ type: "SET_IS_FETCHING_NEW", payload: true })
  }

  return (
    <Selects
      dropzone={itemsState.settings.dropzoneSorting}
      labelText={itemsState.settings.sortByLabelText}
      selectId="swp-sortby"
      options={options}
      isLoading={itemsState.isLoading}
      customOnChange={customOnChange}
      defaultValue={findDefaultSelectVal(
        options,
        itemsState.settings.sortBy,
        itemsState.settings.reverse
      )}
    />
  )
}

export default StorefrontSorting
